@media screen and (max-width: 630px) {
  body {
    margin: 0;
  }

  .logo-block {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 72px 79px 45px;
  }

  .busket {
    display: none;
  }

  .search-small {
    grid-row: 1/2;
  }

  .search {
    display: none;
  }

  .line {
    margin: 0;
  }
}

